<template>
  <v-row>
    <v-col>
      <v-sheet height="64">
        <v-toolbar>
          <v-btn color="yellow darken-2 black--text" dark @click.stop="dialog = true">
            Nueva clase
          </v-btn>
          <v-btn outlined class="mr-4 black" @click="setToday">
            Hoy
          </v-btn>
          <v-btn fab text small @click="prev">
            <v-icon small>mdi-arrow-left</v-icon>
          </v-btn>
          <v-btn fab text small @click="next">
            <v-icon small>mdi-arrow-right</v-icon>
          </v-btn>
          <v-toolbar-title class="amber--text">{{ title }}</v-toolbar-title>
          <div class="flex-grow-1"></div>
          <v-menu  bottom right>
            <template v-slot:activator="{ on }">
              <v-btn outlined v-on="on" class="amber black--text">
                <span>{{ typeToLabel[type] }}</span>
                <v-icon right>mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list class="amber">
              <v-list-item @click="type = 'day'" class="black--text">
                <v-list-item-title>Dia</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'week'" class="black--text">
                <v-list-item-title>Semana</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = 'month'" class="black--text">
                <v-list-item-title>Mes</v-list-item-title>
              </v-list-item>
              <v-list-item @click="type = '4day'" class="black--text">
                <v-list-item-title>4 Dias</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-toolbar>
        </v-sheet>

        <!--    Add event dialog -->

        <v-dialog v-model="dialog" max-width="500">
            <v-card>
                <v-container>
                    <v-form @submit.prevent="addEvent">
                    <v-text-field v-model="name" type="text" label="Nombre de la clase (requerido)"></v-text-field>
                    <v-text-field v-model="start" type="date" label="Fecha de inicio (requerido)"></v-text-field>
                    <v-text-field v-model="end" type="date" label="Fecha de finalizacion (Requerido)"></v-text-field>
                    <!-- <v-text-field v-model="color" type="color" label="Color (click para abrir selector de color)"></v-text-field> -->
                    <v-card-text>
                        <v-row
                            align="center"
                            justify="center"
                        >
                            <v-col>
                                Color
                            </v-col>
                            <v-col>
                                <v-btn-toggle>
                                <v-btn color="#FFBF00" @click="color= '#FFBF00'">
                                    <v-icon>mdi-star</v-icon>
                                </v-btn>
                                <v-btn color="#4590C9" @click="color= '#4590C9'">
                                    <v-icon>mdi-speaker</v-icon>
                                </v-btn>
                                <v-btn color="#730f0f" @click="color= '#730f0f'">
                                    <v-icon>mdi-school-outline</v-icon>
                                </v-btn>
                                <v-btn color="#7FFFD4" @click="color= '#7FFFD4'">
                                    <v-icon>mdi-play</v-icon>
                                </v-btn>
                                <v-btn color="#9F2B68" @click="color= '#9F2B68'">
                                    <v-icon>mdi-palette-outline</v-icon>
                                </v-btn>
                                </v-btn-toggle>
                            </v-col>
                        </v-row>
                    </v-card-text>
                    <v-btn type="submit" color="amber black--text" class="mr-4" @click.stop="dialog = false">
                        Crear clase
                    </v-btn>
                    </v-form>
                </v-container>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogDate" max-width="500">
            <v-card>
                <v-container>
                    <v-form @submit.prevent="addEvent">
                    <v-text-field v-model="name" type="text" label="Nombre dela clase (requerido)"></v-text-field>
                    <v-text-field v-model="start" type="date" label="Fecha de inicio (requerido)"></v-text-field>
                    <v-text-field v-model="end" type="date" label="Fecha de finalizacion (Requerido)"></v-text-field>
                    <!-- <v-text-field v-model="color" type="color" label="Color (click para abrir selector de color)"></v-text-field> -->
                    <v-card-text>
                        <v-row
                            align="center"
                            justify="center"
                        >
                            <v-col>
                                Color
                            </v-col>
                            <v-col>
                                <v-btn-toggle>
                                <v-btn color="#FFBF00" @click="color= '#FFBF00'">
                                    <v-icon>mdi-star</v-icon>
                                </v-btn>
                                <v-btn color="#4590C9" @click="color= '#4590C9'">
                                    <v-icon>mdi-speaker</v-icon>
                                </v-btn>
                                <v-btn color="#730f0f" @click="color= '#730f0f'">
                                    <v-icon>mdi-school-outline</v-icon>
                                </v-btn>
                                <v-btn color="#7FFFD4" @click="color= '#7FFFD4'">
                                    <v-icon>mdi-play</v-icon>
                                </v-btn>
                                <v-btn color="#9F2B68" @click="color= '#9F2B68'">
                                    <v-icon>mdi-palette-outline</v-icon>
                                </v-btn>
                                </v-btn-toggle>
                            </v-col>
                        </v-row>
                    </v-card-text>
                    <v-btn type="submit" color="amber black--text" class="mr-4" @click.stop="dialog = false">
                        Crear clase
                    </v-btn>
                    </v-form>
                </v-container>
            </v-card>
        </v-dialog>

            <v-sheet height="600">
                <v-calendar
                ref="calendar"
                v-model="focus"
                color="yellow darken-2"
                :weekdays="[1, 2, 3, 4, 5, 6, 0]"
                locale="es"
                :short-weekdays="false"
                :events="events"
                :event-color="getEventColor"
                :event-margin-bottom="3"
                :now="today"
                :type="type"
                @click:event="showEvent"
                @click:more="viewDay"
                @click:date="setDialogDate"
                @change="updateRange"
                ></v-calendar>
                <v-menu
                v-model="selectedOpen"
                :close-on-content-click="false"
                :activator="selectedElement"
                offset-x
                >
                    <v-card color="grey darken-3" :width="350" flat>
                        <v-toolbar :color="selectedEvent.color" dark>
                        <v-btn @click="deleteEvent(selectedEvent.id)" icon>
                            <v-icon>mdi-delete</v-icon>
                        </v-btn>
                        <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                        <div class="flex-grow-1"></div>
                        </v-toolbar>

                        <v-card-text>
                        <form v-if="currentlyEditing !== selectedEvent.id">
                            <v-list>
                                <v-subheader>ASISTENCIA</v-subheader>
                                <v-list-item
                                    v-for = "(student, i) in students"
                                    :key="i"  
                                >
                                <v-list-item-content v-text="student.name +' '+ student.surname"></v-list-item-content>
                                <v-checkbox
                                    v-model="checkboxes[i]"
                                ></v-checkbox>
                                </v-list-item>
                            </v-list>
                        </form>
                    </v-card-text>

                    <v-card-actions>
                        <v-btn text class="white black--text" @click="selectedOpen = false">
                        Cerrar
                        </v-btn>
                        <v-btn class="white black--text" v-if="currentlyEditing !== selectedEvent.id" text @click.prevent="editEvent(selectedEvent)">
                        Editar
                        </v-btn>
                    </v-card-actions>
                    </v-card>
                </v-menu>
            </v-sheet>
        </v-col>
    </v-row>
</template>

<script>
import { db } from '@/main'
export default {
    data: () => ({
        today: new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000).toISOString().substr(0, 10),
        focus: new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000).toISOString().substr(0, 10),
        type: "month",
        typeToLabel: {
            month: "Mes",
            week: "Semana",
            day: "Dia",
            "4day": "4 Dias"
        },
        name: null,
        start: null,
        end: null,
        color: "#1976D2",
        currentlyEditing: null,
        selectedEvent: {},
        selectedElement: null,
        selectedOpen: false,
        events: [],
        students: [],
        checkboxes: [],
        dialog: false,
        dialogDate: false
    }),
    mounted() {
        console.log(this.today)
        this.getEvents();
        this.getStudents();
    },
    computed: {
        title () {
            const { start, end } = this
                if (!start || !end) {
                return ''
            }
        const startMonth = this.monthFormatter(start)
        const endMonth = this.monthFormatter(end)
        const suffixMonth = startMonth === endMonth ? '' : endMonth
        const startYear = start.year
        const endYear = end.year
        const suffixYear = startYear === endYear ? '' : endYear
        const startDay = start.day + this.nth(start.day)
        const endDay = end.day + this.nth(end.day)
        switch (this.type) {
            case 'month':
                return `${startMonth} ${startYear}`
            case 'week':
            case '4day':
                return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`
            case 'day':
                 return `${startMonth} ${startDay} ${startYear}`
            }
            return ''
        },
        monthFormatter () {
            return this.$refs.calendar.getFormatter({
                timeZone: 'UTC', month: 'long',
            })
        }
    },
    methods: {
        async getEvents() {
            let snapshot = await db.collection("calEvent").get();
            let events = [];
            snapshot.forEach(doc => {
                let appData = doc.data()
                appData.id = doc.id
                events.push(appData)
            });
            this.events = events
        },
        async getStudents() {
            let snapshot = await db.collection("students").get();
            let students = [];
            snapshot.forEach(doc => {
                let appData = doc.data()
                appData.id = doc.id
                students.push(appData)
            });
            this.students = students
        },
        async addEvent() {
            if(this.name && this.start && this.end) {
                await db.collection('calEvent').add({
                    name: this.name,
                    start: this.start,
                    end: this.end,
                    color: this.color
                })
                this.getEvents()
                this.name = ""
                this.start = ""
                this.end = ""
                this.color = "#1976D2"
            } else {
                alert('Name, start and end date are required')
            }
        },
        editEvent(ev) {
            this.currentlyEditing = ev.id
        },
        async deleteEvent(ev){
            await db.collection('calEvent').doc(ev).delete()
            this.selectedOpen = false
            this.currentlyEditing = null
            this.getEvents()
        },
        showEvent ({ nativeEvent, event }) {
            const open = () => {
                this.selectedEvent = event
                this.selectedElement = nativeEvent.target
                setTimeout(() => this.selectedOpen = true, 10)
            }
            if (this.selectedOpen) {
                this.selectedOpen = false
                setTimeout(open, 10)
            } else {
                open()
            }
            nativeEvent.stopPropagation()
        },
        getEventColor(ev) {
            return ev.color
        },
        setDialogDate( { date }) {
            this.dialogDate = true
            this.focus = date
        },
        updateRange ({ start, end }) {
            this.start = start
            this.end = end
        },
        viewDay ({ date }) {
            this.focus = date
            this.type = 'day'
        },
        setToday () {
            this.focus = this.today
        },
        prev () {
            this.$refs.calendar.prev()
        },
        next () {
            this.$refs.calendar.next()
        },
        nth (d) {
            return d > 3 && d < 21
            ? 'th'
            : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
        }
    }
}
</script>